.prduct-container{
    margin-top: 50px;
    overflow-x: hidden;
}

.product-des{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 10px;
    height: 320px;
    width: 400px
}
.product-des img{
    height: 200px;
    width: 200px;

    
}
.card-body{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    text-align: center;
    flex-direction: column;
}
span{
    color: black;
    font-weight: folder;
    margin-right:2px ;
}

@media only screen and (max-width:991px){

    .product-container{
        overflow-x: hidden;

      
    
    }
}
@media only screen and (max-width:768px){
   .col-md-3{
    width: 30%;
   }
   .col-md-9{
    width: 70%;
   }
}
@media only screen and (max-width:550px){
    .product-container{
        overflow-x: hidden;
    }
    .col-md-3{
       width: 100%;
       padding: 0px;
    }
    .col-md-9{
        width: 100%;
        padding: 0px;
        margin-left: 15px;
    }
}